* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.header-conteiner {
  margin: 0 16px;
  height: 54px;
  /* background-color: blueviolet; */

  display: flex;
  align-items: center;
  justify-content: space-between;
}

.conteiner-wrapper {
  display: flex;
  align-items: center;
  gap: 19px;
}

.icon {
  height: 20px;
  width: 24px;
  display: block;
  background-size: cover;
  background-position: center;
}

.icon--menu {
  background-image: url(../../image/burger.svg);
  background-repeat: no-repeat;
}

.createBt {
  width: 85px;
  height: 32px;
  border: 1px solid #d13980;
  border-radius: 6px;
  background-color: #d13980;
  color: #ffffff;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-size: 14px;
}

.createBt:hover {
  border: 1px solid #882654;
  background-color: #882654;
  color: #dfdede;
}

.title {
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 700;
  font-size: 30px;
  color: #d13980;
}

.user {
  width: 32px;
  height: 32px;
}

.main {
  padding: 0 16px;
  background-color: #1e1e3e;
}

.main-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 16px 0 20px 0;
}

.main-title {
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 700;
  font-size: 18px;
  color: #fff;
  margin: 0;
}

.sortBy {
  width: 102px;
  height: 32px;
  border: 1px solid #d13980;
  border-radius: 6px;
  background-color: transparent;
  color: #d13980;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-size: 14px;
}

.sortBy:hover {
  border: 1px solid #882654;
  background-color: #882654;
  color: #fff;
}

.plus {
  color: #2e2e54;
}

.card-quiz {
  background-color: #fff;
  border: 1px solid #fff;
  border-radius: 6px;
  padding: 12px;
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-bottom: 16px;
}

.wrapper-name {
  display: flex;
  align-items: center;
  gap: 8px;
}

.qiuz-info {
  display: block;
}

.quiz-img {
  width: 99px;
  height: 56px;
}

.qiuz-title {
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 700;
  font-size: 14px;
  color: #2e2e54;
}

.wraper-count-cal {
  display: flex;
  justify-content: space-between;
}

.wraper-date-star {
  display: flex;
  gap: 24px;
}

.date {
  display: flex;
  gap: 8px;
}

.date-des {
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-size: 12px;
  color: #2e2e54;
}

.wrapper-count {
  display: flex;
  gap: 8px;
  justify-content: space-between;
}

.count-des {
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-size: 12px;
  color: #2e2e54;
}

.stars {
  display: flex;
  gap: 2px;
}

.stars__star {
  width: 16px;
  height: 16px;
  background-image: url(../../image/icon/star.svg);
  background-position: center;
  background-repeat: no-repeat;
  transition: filter 0.2s;
  filter: grayscale(100%) brightness(0.7);
}

.stars__star:hover {
  filter: none;
}

.qiuz-lessons {
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-size: 12px;
  color: #2e2e54;
  margin: 8px 0 0 0;
}

.percentage {
  width: 107px;
  height: 21px;
  background-color: #d0893940;
  border-radius: 50px;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 700;
  font-size: 10px;
  color: #2e2e54;
  align-items: center;
  display: flex;
  justify-content: center;
  color: #d08939;
  margin: 0 0 3px 0;
}

.wrapper-start {
  display: flex;
  gap: 8px;
}

.qiuz-info-about {
  display: flex;
  justify-content: space-between;
}

.start-der {
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-size: 12px;
  color: #69748c;
}

.start-date {
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-size: 12px;
  color: #2e2e54;
}

.qiuz-moder {
  display: flex;
  align-items: center;
  gap: 8px;
}

.moder {
  background-color: #2e2e54;
  width: 24px;
  height: 24px;
  border: 1px solid #69748c;
  border-radius: 50px;
  color: #fff;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 700;
  font-size: 10px;
  align-items: center;
  display: flex;
  justify-content: center;
}

.moder-title {
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-size: 14px;
  color: #2e2e54;
}

.moder-name {
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-size: 14px;
  color: #69748c;
}

.bt-wrapper {
  display: flex;
  gap: 8px;
}

.share {
  width: 44px;
  height: 44px;
  border: 1px solid #d2d4d8;
  border-radius: 6px;
  background-color: transparent;
  align-items: center;
  display: flex;
  text-align: center;
  justify-content: center;
}

.share:hover {
  border: 1px solid #a4a5a7;
  background-color: #a4a5a7;
}

.massenge {
  width: 44px;
  height: 44px;
  border: 1px solid #d2d4d8;
  border-radius: 6px;
  background-color: transparent;
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
}

.massenge:hover {
  border: 1px solid #a4a5a7;
  background-color: #a4a5a7;
}

.start {
  width: 200px;
  height: 44px;
  border: 1px solid #d13980;
  border-radius: 6px;
  background-color: #d13980;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-size: 14px;
  color: #ffffff;
}

.start:hover {
  border: 1px solid #882654;
  background-color: #882654;
  color: #dfdede;
}

.footer{
 position: fixed;     /* фіксуємо */
  bottom: 0;           /* притискаємо до низу */
  left: 0;             /* від лівого краю */
  width: 100%;         /* на всю ширину */
  height: 48px;
  background-color: #2E2E54;
  z-index: 1000;
}